import React, { useEffect, useState } from 'react';
import tlg from '../../assets/icons/tlg-icon.svg';
import { useCookies } from 'react-cookie';
import GetServices from "../../api/getServices";

import styles from './main.module.scss';
 

 


function HashTable() {
  const [cookies, setCookie] = useCookies(['accessToken']);
  const [error, setError] = useState({});
  const [popup, setPopup] = useState(false); 
  const [hash, setHash] = useState('');
  const [id, setId] = useState('');
  const [data, setData] = useState({});


  async function getArray(data){
    const array  = await GetServices.getData(`https://ce50348.tw1.ru/reader`, data)
     console.log(array.data);
  }
  useEffect(() => {
    setData(
      JSON.stringify(
        {
          hash: hash,
          id: id
        }))},[hash, id]) 

  let handleClick = (e)=>{
    e.preventDefault();

    if(cookies) {
     

    console.log(cookies.name, data);
    getArray(data);
    }else{
      setPopup(true);
    }
  }
  const handlePopupClick = (e) => {
    if (e.target === e.currentTarget) {
      setPopup(false);
    }
  };

  return (
    <div className={styles.wrapper}>
      <div className={styles.content}>
      <div className={styles.content_container}>
        <h1 className={styles.title}><span></span><span>Telegram Stories</span></h1>
        <p className={styles.subtitle}>    id Telegram</p>
        <form className={styles.hash_form}>
          <div className={styles.input_container}>
            <input name='hash' onInput={(e)=>setHash(e.target.value)} value={hash} className={error.msg?styles.error:styles.input} type='text' placeholder=' hash'/>
            <input name='id' onInput={(e)=>setId(e.target.value)} value={id} className={error.msg?styles.error:styles.input} type='number' placeholder=' id'/>
            {error.msg&&<div className={styles.error_msg}>{error.msg}</div>}
           </div>

          <button className={styles.btn} onClick={(e)=>handleClick(e)}></button>        

        </form>
      </div>
      </div>
      <div className={styles.footer}>
      <div className={styles.copyright}>
       2023 Stories Analytics
    </div>
    <div className={styles.links}>
      <a href='#'> </a>
      <a href='#'> </a>
      </div>
      </div>
    {popup&&<div className={styles.popup} onClick={handlePopupClick}>
      <div className={styles.popup_content}>
        <div className={styles.title_wrapper}>
          <div className={styles.popup_title}> </div >
          <div className={styles.exit} onClick={()=>setPopup(false)}>+</div>
        </div>
        <button className={styles.btn} >
        <img src={tlg} alt="telegram icon"/>   
          </button>

      </div >
    </div >}
  </div>
  );
}

export default HashTable;
